<template>
  <div class="app">
    <input type="file" @change="onChange" />
    <el-button type="primary" @click="exportExcel">导出excel</el-button>
    <el-table
      v-if="excelData.results"
      :data="[...excelData.results]"
      border
      highlight-current-row
      style="width: 95%; margin: 10px"
    >
      <el-table-column
        align="center"
        v-for="item of excelData.header"
        :key="item"
        :prop="item"
        :label="item"
      >
        <template slot-scope="{ row, index }">
          <el-input
          
            v-model="row[item]"
            placeholder=""
            v-if="row.edit && item!=='序号'"
          ></el-input>
          <span v-else>{{row[item]}}</span>
        </template>
      </el-table-column>
      <el-table-column
        header-align="center"
        align="center"
        prop=""
        label="操作"
      >
        <template slot-scope="{row}">
          <el-button type="warning" @click="row.edit = true" v-if="!row.edit" size="medium">修改</el-button
        ><el-button type="success" v-else size="medium" @click="save(row)">保存</el-button>
        <el-button type="primary" v-if="row.edit" size="medium" @click="row.edit=false">取消</el-button>
        </template>
        </el-table-column
      >
    </el-table>
  </div>
</template>

<script>
import XLSX from "xlsx";
// import XLSXS from "xlsx-style";
export default {
  name: "app",
  data() {
    return {
      excelData: {
        header: null,
        results: null,
      },
    };
  },
  methods: {
    async onChange(e) {
      // console.log(e.target.files[0]);
      await this.readerExcel(e.target.files[0]);
    },
    generateData({ header, results }) {
      this.excelData.header = header;
      this.excelData.results= results.map(item => {
        this.$set(item,'edit',false)
        return item
      })
    },
    readerExcel(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsArrayBuffer(file);
        reader.onload = (ev) => {
          const data = ev.target.result;
          const workbook = XLSX.read(data, { type: "array" }); //以数组读取文件
          console.log(workbook);

          const firstSheetName = workbook.SheetNames[0]; //获取单元格信息

          const worksheet = workbook.Sheets[firstSheetName];
          const header = this.getHeaderRow(worksheet); //获取表头
          console.log(firstSheetName);
          console.log(worksheet);
          console.log(worksheet["!merges"]);
          console.log(header);
          const results = XLSX.utils.sheet_to_json(worksheet);
          console.log(results);
          this.generateData({ header, results });
        };
      });
    },
    getHeaderRow(sheet) {
      let headers = [];
      const range = XLSX.utils.decode_range(sheet["!ref"]); //获取excel表格的总范围 A1:I3 转为 end start
      console.log(range);
      let C;
      const R = range.s.r; // 0
      /* start in the first row */
      for (C = range.s.c; C <= range.e.c; ++C) {
        //循环列
        /* walk every column in the range */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })];
        /* find the cell in the first row */
        let hdr = "UNKNOWN " + C; // <-- replace with your desired default
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);
        headers.push(hdr);
      }
      headers = headers.filter((item) => !item.includes("UNKNOWN"));
      return headers;
    },

    exportExcel() {
      
      let sheet = XLSX.utils.json_to_sheet(this.excelData.results.map(item => {
         const {edit,...newItem} = item
         return newItem
        // return item.includes('edit')
      }));
      let book = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(book, sheet, "11月份");
      XLSX.writeFile(book, `${new Date().getTime()}.xlsx`);
    },
    // exportExcel(){

    // }
    save(row){
      row.edit = false;
      this.$message.success('保存成功')
    }
  },
};
</script>

<style scpoed>
* {
  margin:0;
  padding:0;
}
</style>